<!--  -->
<template>
  <div class="cartbtn">
    <transition name="move">
      <div class="cart-decrease" v-show="food.count>0"
        @click.stop.prevent="removeCart($event)"><span class="inner icon-remove_circle_outline"></span></div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <div class="cart-add icon-add_circle " @click.stop.prevent="addCart($event)"></div>
  </div>
</template>

<script>
import Vue from 'vue';
export default {
  props: ['food'],
  created() {
    console.log(this.food);
  },
  methods:{
    addCart(event){
      if(!event._constructed) return
      console.log('click')
      if(!this.food.count){
        Vue.set(this.food,'count',1)
      }else {
        this.food.count++
      }
      this.$emit('addball',event.target) //向外传递dom对象
    },
    removeCart(){
      if(!event._constructed) return
      if(this.food.count){
        this.food.count--
      }
    }
  }
};
</script>
<style lang='scss' >
@import '../../assets/scss/index.scss';
.cartbtn {
  font-size:0;
  .cart-decrease {
    display: inline-block;
    padding: 6px;
    opacity: 1;
    transform: translate3D(0,0,0);
    .inner {
          display: inline-block;
          line-height: 24px;
          font-size: 24px;
          color: rgb(0, 160, 220);
          transform: rotate(0);
      }
    &.move-enter-active,&.move-leave-active {
      transition:all .5s linear; 
    }
    &.move-enter,&.move-leave-active {
      opacity: 0;
      transform: translateX(24px);
      .inner {
         transform: rotate(180deg);
      }
         

    }
    // .inner {
    //   display: inline-block;
    //   padding: 6px;
    //   font-size:24px;
    //   line-height:24px;
    //   color:rgb(0,160,220);
    // }
    
  }
  .cart-count {
    vertical-align: top;
    display: inline-block;
    font-size: 10px;
    width:12px;
    padding-top:6px;
    line-height:24px;
    text-align: center;
    color:rgb(147,153,159);
    
  }
  .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: rgb(0, 160, 220);
    
  }
}
</style>


